import gsap from 'gsap';
import ScrollToPlugin from 'gsap/ScrollToPlugin';
import { exist } from '../utils';
import { DEFAULT_GSAP_EASE } from '../utils/constants';

const anchorLinks = () => {
	const $anchors = document.querySelectorAll('.js-anchor');

	if (exist($anchors.length)) {
		gsap.registerPlugin(ScrollToPlugin);

		$anchors.forEach($anchor => {
			$anchor.addEventListener('click', (e) => {
				e.preventDefault();

				const blockClass = $anchor.dataset?.scrollTo;
				const $block = document.querySelector(blockClass);

				const $header = document.querySelector('.header');

				const offsetY = (exist($header) ? $header.offsetHeight : 0) + 30;

				if (!exist($block)) return;

				gsap.to(window, {
					scrollTo: {
						y: $block,
						autoKill: false,
						offsetY,
					},
					ease: DEFAULT_GSAP_EASE,
				});
			});
		});
	}
};

export default anchorLinks;
